:host {
  width: 100%;
  height: 100%;
}

#player-video {
  width: 100%;
}

#webrtcfeeds-component {
  .screen-card {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    display: flex;
    flex-direction: column;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.93);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    padding: 52px 10px 10px 10px;
    max-width: 820px;
    width: 820px;

    .logo {
      display: flex;
      justify-content: center;

      img {
        width: 176px;
        height: 40px;
      }
    }

    .mfr-title {
      color: #000;
      font-size: 20px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      padding-top: 36px;
      text-align: center;
    }

    .mfr-content {
      line-height: normal;
      padding-top: 15px;
      text-align: center;
    }

    .input-heading {
      padding-top: 18px;
      color: red;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      text-align: center;
    }

    .form-group {
      padding-top: 20px;
      margin-bottom: 0;

      mat-form-field {
        width: 100%;
      }
    }

    .checkbox-div {
      display: flex;
      gap: 8px;
      align-items: center;

      input {
        border-radius: 5px;
        border: 1px solid #9d9fa2;
        background: #fff;
        width: 20px;
        height: 20px;
      }

      .checkbox-text {
        color: #000;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
      }
    }
  }

  .term-of-services {
    bottom: 0%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    color: var(--basic-white, #fff);
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;

    a {
      text-decoration-line: underline;
      cursor: pointer;
    }

    .version-text {
      padding-top: 24px;
    }
  }
}

.bg-image {
  background-image: url('../../../../assets/img/default-login.jpg');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;

  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(39, 52, 59, 0.7);
  }
}

::ng-deep {
  .mat-form-field.mat-focused .mat-form-field-outline-thick {
    color: #2cace2 !important;
  }

  .mat-form-field.mat-focused .mat-form-field-label {
    color: #2cace2 !important;
  }

  .mat-mdc-form-field.mat-focused .mdc-notched-outline {
    border-color: #2cace2 !important;
  }

  /* Change the label color when focused */
  .mat-mdc-form-field.mat-focused .mdc-floating-label {
    color: #2cace2 !important;
  }

  .mdc-text-field--outlined:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-notched-outline__trailing {
    border-color: #2cace2 !important;
  }
  .mdc-text-field--outlined:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-notched-outline__notch {
    border-color: #2cace2 !important;
  }
  .mdc-text-field--outlined:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-notched-outline__leading {
    border-color: #2cace2 !important;
  }

  .mat-mdc-form-field-input-control.mat-mdc-form-field-input-control {
    font-size: 14px !important;
    color: #000 !important;
  }
}

